import Slider from './slider'

export default class MainSlider extends Slider {
	constructor(btns) {
		super(btns)
	}

	showSlides(n) {
		if (n > this.slides.length) {
			this.slideIndex = 1
		}

		if (n < 1) {
			this.slideIndex = this.slides.length
		}

		// скрываем на 3-м слайде модалку
		try {
			this.hanson.style.opacity = '0'

			if (n === 3) {
				this.hanson.classList.add('animate__animated')
				setTimeout(() => {
					this.hanson.style.opacity = 1
					this.hanson.classList.add('animate__fadeInUp')
				}, 3000)
			} else {
				this.hanson.classList.remove('animate__fadeInUp')
			}
		} catch (e) {}

		Array.from(this.slides).forEach((slide) => {
			slide.style.display = 'none'
		})

		this.slides[this.slideIndex - 1].style.display = 'block'
	}

	plusSlides(n) {
		this.showSlides((this.slideIndex += n))
	}

	bindTriggers() {
		this.btns.forEach((item) => {
			item.addEventListener('click', () => {
				this.plusSlides(1)
			})

			item.parentNode.previousElementSibling.addEventListener('click', (e) => {
				e.preventDefault()
				this.slideIndex = 1
				this.showSlides(this.slideIndex)
			})
		})

		document.querySelectorAll('.prevmodule').forEach((item) => {
			item.addEventListener('click', (e) => {
				e.stopPropagation()
				e.preventDefault()
				this.plusSlides(-1)
			})
		})

		document.querySelectorAll('.nextmodule').forEach((item) => {
			item.addEventListener('click', (e) => {
				// stopPropagation отмена двойного нажания на next (всплытие событий!)
				e.stopPropagation()
				e.preventDefault()
				this.plusSlides(1)
			})
		})
	}

	render() {
		if (this.container) {
			// обработчик ошибки для ....hanson
			try {
				this.hanson = document.querySelector('.hanson')
			} catch (e) {}

			this.showSlides(this.slideIndex)
      this.bindTriggers()
		}
	}
}
